function parseNamespacedConfig(result, name, config) {
  var actions = config.actions,
      effects = config.effects,
      onInitialize = config.onInitialize,
      state = config.state;

  if (actions) {
    result.actions[name] = actions;
  }

  if (effects) {
    result.effects[name] = effects;
  }

  if (state) {
    result.state[name] = state;
  }

  if (onInitialize) {
    result.initializers[name] = onInitialize;
  }
}

export function namespaced(namespaces) {
  var result = {
    initializers: {},
    actions: {},
    effects: {},
    state: {}
  };
  Object.keys(namespaces).forEach(function (name) {
    parseNamespacedConfig(result, name, namespaces[name]);
  });
  return Object.assign({
    actions: result.actions,
    effects: result.effects,
    state: result.state,
    onInitialize: function onInitialize(context, app) {
      return Promise.all(Object.keys(result.initializers).map(function (key) {
        return result.initializers[key](Object.assign({}, context, {
          execution: Object.assign({}, context.execution, {
            namespacePath: context.execution.namespacePath.concat(key)
          })
        }), app);
      }));
    }
  });
}